import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  root: {
    margin: "40px 0 20px 0"
  },
  hr: {
    border: "0",
    borderTop: "1px solid #3d3d3d"
  },
  separator: {
    color: "#3d3d3d"
  },
  navigation: {
    float: "right",
  }
});

const Header = () => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <hr className={classes.hr} />
      <Grid container>
        <Grid item xs={6}>
          <div>
            <Typography variant="caption" color="secondary" align="center">
              <strong>MISSION: </strong> 
            </Typography>
            <Typography variant="caption" gutterBottom>
              Lead digital asset managment for virtual and real world spaces.
            </Typography>
          </div>
          <Typography variant="caption" color="secondary" gutterBottom>
            {"\u00A9"} 2020 Mythical Corporation. or its affiliates.
          </Typography>
        </Grid>
        <Grid item xs={6}>
          <div className={classes.navigation}>
            <Link href="/conditions">Conditions of Use</Link>
            <span className={classes.separator}> {"\u007C"}  </span>
            <Link href="/privacy-notice">Privacy Notice</Link>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export default Header;
