import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles({
  missionStatement: {
    textAlign: "center"
  },
  signatureImage: {
    display: "block",
    float: "right",
  },
  signature: {
    textAlign: "right",
    margin: "0 40px"
  }
});

const About = () => {
  const classes = useStyles();
    return (
      <Grid container>
        <Grid item xs={12}>
          <Typography variant="h4" component="h1" className={classes.missionStatement}>
            <blockquote>
              "We are on a mission to be the leader of digital asset managment for virtual and real world spaces."
            </blockquote>
          </Typography>
          <div className={classes.signature}>
            <img src="/about/signature.png" alt="Signature" />
          </div>
          <div className={classes.signature}>
            <span>- Andrew Kharlamov</span>
          </div>
          <div className={classes.signature}>
            <Typography variant="caption" color="secondary">
              Founder, CEO
            </Typography>
          </div>
        </Grid>
      </Grid>
    );
};

export default About;
