import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

const Technology = () => {
  return (
    <Grid container spacing={4}>
      <Grid item xs={12}>
        <Typography variant="h3" component="h1" >
          <Box textAlign="center">
            Technology
          </Box>
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <p>
          Here at Mythical Corporation each member of our team has a passion for their skillset. 
          Whether its sanding down a finished sculpture, pouring a fresh batch of resin into a printer, 
          or working on cloud solutions to deliver quality assets to our users; Our employees live to push 
          technology to the boundries of our industries.
        </p>
      </Grid>
      <Grid item xs={12}>
        <Typography variant="h6" component="h2" gutterBottom>
          <strong>
            Our Assets Built With
          </strong>
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <Grid container>
          <Grid item xs={12} sm={6} md={3}> 
            <figure>
              <img src="/technology/adobe_logo.png" alt="Adobe Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    Adobe
                  </strong>
                </Typography>
                Adobe's leading graphic tools and cloud platform help us deliever quality 2D assets to our users.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={12} sm={6} md={3}> 
            <figure>
              <img src="/technology/blender_logo.png" alt="Blender Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    Blender
                  </strong>
                </Typography>
                Blender offers us a complete feature rich 3D creation pipeline for modeling, rigging, animation, and rendering of our assets.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={12} sm={6} md={3}> 
            <figure>
              <img src="/technology/zbrush_logo.png" alt="ZBrush Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    ZBrush
                  </strong>
                </Typography>
                ZBrush is an industry standard digital sculpting tool. This tool allows us to create some of the most detailed digital assets available in our industry.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={12} sm={6} md={3}> 
            <figure>
              <img src="/technology/substance_logo.png" alt="Substance Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    Substance
                  </strong>
                </Typography>
                Substance now owned by Adboe is a lead texturing solution that allows us to provide our users with rich assets.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={12} sm={6} md={3}> 
            <figure>
              <img src="/technology/unreal_logo.png" alt="Unreal Engine Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    Unreal Engine
                  </strong>
                </Typography>
                With Unreal Engine's open and advanced real-time 3D creation tooling we are given the ability to push our content's quality.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={12} sm={6} md={3}> 
            <figure>
              <img src="/technology/unity_logo.png" alt="Unity Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    Unity
                  </strong>
                </Typography>
                Unity is a real-time 3D development platform that allows us to create stunning animations and game renders.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={12} sm={6} md={3}> 
            <figure>
              <img src="/technology/marmoset_logo.png" alt="Marmoset Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    Marmoset
                  </strong>
                </Typography>
                Marmoset is the industry leading real-time renderer, it's platform allows us to share our assets with our users. 
              </figcaption>
            </figure>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Typography variant="h6" component="h2" gutterBottom>
          <strong>
            Our Systems Run On
          </strong>
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <Grid container>
          <Grid item xs={3}> 
            <figure>
              <img src="/technology/aws_logo.png" alt="AWS Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    AWS
                  </strong>
                </Typography>
                Amazon Web Services (AWS) is the leading cloud platform. It allows us to host and scale our businesses while providing high availability.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={3}> 
            <figure>
              <img src="/technology/netcore_logo.png" alt="Dot Net Core" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    .NET Core
                  </strong>
                </Typography>
                The .NET Core framework allows us to build robust cross platform server applications.
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={3}> 
            <figure>
              <img src="/technology/node_logo.png" alt="Node JS Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    Node
                  </strong>
                </Typography>
                Node.js is a JavaScript runtime, designed to allow us to build scalable network applications. 
              </figcaption>
            </figure>
          </Grid>
          <Grid item xs={3}> 
            <figure>
              <img src="/technology/react_logo.png" alt="React Logo" />
              <figcaption>
                <Typography variant="h5" component="h3" gutterBottom>
                  <strong>
                    React
                  </strong>
                </Typography>
                React allows us to design quality application interfaces. 
              </figcaption>
            </figure>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <p>
          We wanted to take the time to give a special thanks to the companies listed on this page for supporting and helping with our growth.
        </p>
      </Grid>
    </Grid>
  );
};

export default Technology;
