import React from 'react';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import 'typeface-roboto';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Header from './Components/Header';
import Footer from './Components/Footer';
import Home from './Routes/Home';
import About from './Routes/About';
import Conditions from './Routes/Conditions';
import Press from './Routes/Press';
import Services from './Routes/Services';
import Technology from './Routes/Technology';
import PrivacyNotice from './Routes/PrivacyNotice';
import './styles.css';

const theme = createMuiTheme({
  palette: {
    type: "dark",
    primary: {
      main: "#b8b8b8",
    },
    secondary: {
      main: "#8a8a8a"
    },
    background: {
      default: "#292929"
    },
  },
  typography: {
    fontFamily: [
      "Calibri",
      "Roboto",
    ].join(','),
  },
  overrides: {
    MuiCssBaseline: {
      "@global": {
        body: {
          backgroundImage: "url('/clouds.png')",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "top center"
        },
      }
    }
  }
});

const App = () => {
  return (
    <Router>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Container maxWidth="md">
          <Grid container>
            <Grid item xs={12}>
              <Header />
            </Grid>
            <Grid item xs={12}>
              <Grid container>
                <Grid item xs={12}>
                  <Switch>
                    <Route path="/services"><Services/></Route>
                    <Route path="/technology"><Technology/></Route>
                    <Route path="/about"><About/></Route>
                    <Route path="/conditions"><Conditions/></Route>
                    <Route path="/press"><Press/></Route>
                    <Route path="/privacy-notice"><PrivacyNotice/></Route>
                    <Route path="/"><Home/></Route>
                  </Switch>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <Footer/>
            </Grid>
          </Grid>
        </Container>
      </ThemeProvider>
    </Router>
  );
}

export default App;
